
export default {
    state:{
        onlineIds:{},
        chat:[
            // {
            //     sender:'222222222',
            //     userlever: 22,
            //     viplevel: 18,
            //     content: new Date(),
            //     stamp: 1527080970,
            // },{
            //     sender:'222222222',
            //     userlever: 22,
            //     viplevel: 18,
            //     content: new Date() + new Date(),
            //     stamp: 1527080970,
            // }
        ],
        gifts:[
            // {
            //     sender:'小明',
            //     userlever: 22,
            //     viplevel: 18,
            //     reciver: '小红',
            //     gift: '超级火箭',
            //     quantity: 5 + Math.round(Math.random()*(20-5)),
            //     stamp: 1527080910,
            // },
            // {
            //     sender:'小明',
            //     userlever: 22,
            //     viplevel: 18,
            //     reciver: '小红',
            //     gift: '超级火箭',
            //     quantity: 5 + Math.round(Math.random()*(20-5)),
            //     stamp: 1527080920,
            // },
        ],
        //  公告队列
        notifys:[]
    },
    mutations:{
        pushOnlineId(state,params){
            state.onlineIds[params.name] = params.value;
        },
        removeOnlineId(state,name){
            delete state.onlineIds[name];
        },
        getOnlineId(state,name){
            return state.onlineIds[params.name];
        },
        addChat(state,data){
            state.chat.push(data);
            //  清除过多的弹幕节点
            if(state.chat.length > 150){
                state.chat.splice(0,30);
            }
        },
        addGifts(state,data){
            state.gifts.push(data);
            //  清除过多的礼物节点
            if(state.gifts.length > 150){
                state.gifts.splice(0,30);
            }
        },
        addNotify(state,data){
            state.notifys.concat(data);
            state.notifys.sort((a,b) => {   //  排序
                return a.stamp - b.stamp;
            });
        },
        removeNotify(state){
            return state.notifys.shift();  // 弹出队头
        },
        getFirstNotify(state){
            return state.notifys[0] || null;  // 弹出队头
        },
    }
}


