<template>
    <div class="show-app app-container">
		<video-component :video="video" @startPlay.once="videoPlay()" @endPlay.once="videoEnd()"></video-component>
        <notice-component :word="notice" v-show="notice"></notice-component>
        <component :is="currentComponent"></component>
	</div>
</template>
<script>

import VideoComponent from '@/components/VideoComponent.vue'
import NoticeComponent from '@/components/NoticeComponent.vue'
import WaitComponent from '@/components/WaitComponent.vue'
import TabComponent from '@/components/TabComponent.vue'

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    name: 'ShowPage',
    data(){
        return {
            video:{
                src: './static/media/test.mp4',
                poster: '',
                type: 'application/x-mpegURL',
            },
            notice: null,
            currentComponent: null
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,
            onlineIDs: state => {
                const obj = state.HeartBeanService.onlineIds;
                return [obj.web,obj.live,obj.vod]
            }
        })
    },
    watch:{
        baseInfo: function(data){
            if(data.airtime && data.airtime > parseInt(new Date().getTime()/1000)){
                this.currentComponent = 'wait-component'
            }else{
                this.currentComponent = 'tab-component'
            }
        }
    },
    methods:{
        //  触发视频播放，开始在线计费
        videoPlay: function(){
            var _this = this;
            this.$store.dispatch('startOnlineJson',{
                objType: 'live',
                objId: this.chnid
            }).then(({data})=>{
                if(data.success == "true"){
                    //  提交视频播放的 onlineID
                    _this.$store.commit('pushOnlineId',{
                        name: 'live',
                        value : data.onlineID
                    });
                }else{
                    _this.$store.commit('alert',{
                        text: data.msg
                    })
                }
            })
        },
        //  触发视频结束，结束在线计费
        videoEnd(){
            this.$store.dispatch('stopOnlineJson',this.$commit('getOnlineId','live')).then(({data})=>{
                if(data.success == "true"){
                    //  删除视频播放的 onlineID
                    this.$store.commit('removeOnlineId',data.onlineID);
                }else{
                    this.$store.commit('alert',{
                        text: data.msg
                    })
                }
            })
        }
    },
    components:{
        VideoComponent,
        NoticeComponent,
        WaitComponent,
        TabComponent    
    },
    mounted: function(){
        this.$nextTick(()=>{
            this.$store.commit('setChnid',this.$route.query.chnid);
            
            //  获取基础信息构造参数
            const params = {chnid: this.$route.query.chnid}
            // 获取可选额外参数
            if(this.$route.query.vod){ params.vod = this.$route.query.vod;}
            if(this.$route.query.rcm){ params.rcm = this.$route.query.rcm;}

            //  获取基础信息
            this.$store.dispatch('getRoomInitialJson',params).then(({data}) => {
                if(data.noright){
                    //  无权访问
                    // alert(data.noright.msg);
                    // window.location.href = data.noright.link;
                    return false;
                }else{
                    //  保存基础信息
                    this.$store.commit('setBaseInfo',data);
                    //  设置页面信息
                    this.video.poster = this.ApiConfig.domain + data.topimg;
                    document.title = data.title;
                    //  提交web onlineId
                    this.$store.commit('pushOnlineId',{
                        name: 'web',
                        value : data.onlineID
                    });

                    //  建立心跳通讯
                    const startHeart = () =>{
                        this.$store.dispatch('getHeartbeatJson',{
                            onlineID: this.onlineIDs
                        }).then(({data}) => {
                            //  ===================================
                            //  荡母发送设置
                            //  ===================================
                            const chat =  [];   //  聊天队列
                            let i = 0;
                            const startChat = () =>{
                                if(chat[i]){
                                    this.$store.commit('addChat',chat[i]);
                                    i++;
                                }else{
                                    clearInterval(chat_timer)
                                }
                            }
                            //  弹幕发送频率定时器：间隔 = 心跳间隔/当前心跳弹幕数目
                            var chat_timer = setInterval(() => {
                                startChat()
                            },Math.floor(interval_timer * 1000 / chat.length));
                            startChat()


                            //  ===================================
                            //  礼物发送设置
                            //  ===================================
                            const gifts =  [];  //  礼物队列
                            let k = 0;
                            const startGift = () =>{
                                if(gifts[k]){
                                    this.$store.commit('addGifts',gifts[k]);
                                    k++;
                                }else{
                                    clearInterval(gift_timer)
                                }
                            }
                            //  弹幕发送频率定时器：间隔 = 心跳间隔/当前心跳弹幕数目
                            var gift_timer = setInterval(() => {
                                startGift()
                            },Math.floor(interval_timer * 1000 / gifts.length));
                            startGift()

                            
                            //  ===================================
                            //  公告发送设置
                            //  ===================================
                            const notify = [];  //  公告队列
                            if(data.broadcast){
                                this.$store.commit('addNotify',data.broadcast);
                            }
                        })  
                    }
                    //  心跳通讯定时器
                    var interval_timer = this.baseInfo.hbinterval;
                    setInterval(() => {
                        startHeart();
                    }, interval_timer * 1000);
                    startHeart();
                }
            })


            //  监听公告定时器
            setInterval(() => {
                const first_notice = this.$store.commit('getFirstNotify');
                if(first_notice.stamp < new Date().getTime()/1000){
                    this.notice = first_notice.content;
                }
            },1000);

            //  获取直播流
            this.$store.dispatch('getLiveUrlInfo',{
                chnid: this.chnid,
            }).then(({data}) => {
                if(data.success == "true"){
                    this.video.src = data.uri
                }else{
                    this.$store.commit('alert',{
                        text: data.msg
                    })
                    // alert(data.msg);
                }
            })
            
        })
    }
}
</script>



